@import url(https://fonts.googleapis.com/css?family=Roboto:500);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

.header {
  position: fixed;
  width: 100%;
  background: #1b242f;
  border-bottom: 3px solid #04c2c9;
  height: 50px;
}

.header-text {
  font-family: 'Noto Sans KR', sans-serif;
  color: white;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  height: 100%;
  max-width: 800px;
}

p {
  margin: 0px 20px 0px 20px;
}
p:hover {
  color: #04c2c9;
  cursor: pointer;
}

/* 모바일 */
@media screen and (max-width: 767px){
  .header {z-index:10;}
  .header p {margin:0 10px;}
}
.typer {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url(/static/media/back.1bb809dd.jpg);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  width: 100%;
}

.typer-text {
  font-family: 'Noto Sans KR', sans-serif;
  border-right: 0.1em solid white;
  font-size: 4vw;
  color: white;
  white-space: pre;
  line-height: 1;
  padding-top: 0.2em;
  -webkit-animation: blink 1s step-end infinite;
          animation: blink 1s step-end infinite;
}

.button {
  font-family: 'Noto Sans KR', sans-serif;
  text-align: center;
  color: white;
  font-size: 2vw;
  width: 20vw;
  height: 8vh;
  background: none;
  border-radius: 12px;
  border: 2px solid #fff;
  margin:20px 0 0;
}

.button:hover {
  cursor: pointer;
  background: #04c2c9;
  transition: all 0.5s;
}

/* 모바일 */
@media screen and (max-width: 767px){
  .typer-text{font-size:5vw;}
  .button {width:50vw;font-size:4vw;}
}
.about {
  display: block;
  background: white;
  width: 100%;
  padding-top: 8vh;
  padding-bottom: 4vh;
}

.about_me_title {
  width: 100%;
  font-family: 'Noto Sans KR', sans-serif;
  text-align: center;
  align-self: center;
  font-size: 4vw;
}

.about_me_layer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 6vh;
}

.about_me_picture {
  width: 35%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 4vw;
}

.strength_layer {
  width: 50%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.strength_size {
  display: flex;
  align-items: center;
  margin-bottom: 2vh;
  margin-top: 2vh;
}

.strength_img {
  text-align: center;
  width: 15vw;
}

.icon {
  width: 5vw;
  height: 5vw;
}

.hedding {
  width: 25vw;
  height: 25vw;
}

/* 모바일 */
@media screen and (max-width: 767px){
  .about_me_picture {display:block;width:100%;margin:0;}
  .hedding {width:100%;height:auto;padding:0 15px;}
  .strength_layer {width:100%;}
  .strength_img {width:100%;}
  .icon {display:inline-block;margin:18px 10px 0 0;vertical-align:top;}
  .strength_size {display:block;}
  h4.ant-typography, .ant-typography h4 {display:inline-block;padding:0;}
  .ant-typography {display:block;padding:0 15px;text-align:center;}
}

.skill_image_container {
  text-align: center;
  display: inline-block;
  width: 114px;
  height: 116px;
  border-radius: 30px 30px;
  background-size: cover;
  background-repeat: no-repeat;
  margin-right: 2rem;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2), 0 3px 6px rgba(0, 0, 0, 0.2);
}
.skill_label {
  display: flex;
  justify-content: center;
  align-items: center;
  background: black;
  border-radius: 30px 30px;
  width: 114px;
  height: 116px;
  padding: 10px;
  opacity: 0.8;
  font-weight: 400;
  font-size: 1rem;
  color: white;
  visibility: hidden;
}

.skill {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  background: rgb(231, 230, 230);
  padding-top: 4vh;
  padding-bottom: 4vh;
}
.skill_title {
  font-family: 'Noto Sans KR', sans-serif;
  text-align: center;
  align-self: center;
  font-size: 4vw;
  margin: 3vh 0 2vh 0;
}

.skill_Content {
  margin-bottom: 20px;
}

.skill_image_container:hover .skill_label {
  visibility: visible;
  animation: fadeout1 0.5s;
  -moz-animation: fadeout1 0.5s; /* Firefox */
  -webkit-animation: fadeout1 0.5s; /* Safari and Chrome */
  -o-animation: fadeout1 0.5s; /* Opera */
}
@keyframes fadeout1 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}
@-webkit-keyframes fadeout1 {
  /* Safari and Chrome */
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}

/* 모바일 */
@media screen and (max-width: 767px) {
  .skill_Content {
    margin: 0;
    padding: 0 15px;
  }
  .skill_image_container {
    width: 27%;
    height: auto;
    margin: 5px;
    background-size: 100% 100%;
  }
  .skill_image_container img {
    width: 100%;
  }
  .skill_image_container:nth-child(3) {
    background-size: 90% 90%;
    background-position: center center;
  }
  .skill_label {
    width: 100%;
    height: 100px;
  }
}

.contentStyle {
  height: 45vh;
  background: #364d79;
}

.textStyle {
  font-size: 1.2em;
  line-height: 2em;
}

.carousel {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 66vh;
  background: #1b242f;
  width: 100%;
  padding-top: 4vh;
}
.carousel_siz {
  width: 45vw;
}

.ant-card-head,
.ant-card-body {
  color: #fff;
}

.ant-card-body .ant-typography {
  color: #fff;
}

/* 모바일 */
@media screen and (max-width: 767px) {
  .carousel {
    display: block;
    height: 450px;
    padding-top: 8vh;
  }
  .carousel_siz {
    width: 100%;
  }
  .ant-card-head,
  .ant-card-body {
    padding: 0 15px;
  }
  .ant-card.contentStyle {
    height: 330px;
    padding: 0 0 15px;
  }
  .textStyle {
    padding: 10px 0;
    font-size: 1.1em;
    line-height: 1.2em;
  }
  .ant-card-body .ant-typography {
    padding: 0;
    font-size: 0.9em;
    font-weight: normal;
    text-align: left;
  }
  .ant-carousel .slick-dots-bottom {
    bottom: 0;
  }
}

.contact {
  font-family: 'Roboto', sans-serif;
  height: 40vh;
  width: 100%;
  text-align: center;
  margin: 0;
  padding: 0;
}
.contact_hedding {
  font-family: 'Noto Sans KR', sans-serif;
  position: relative;
  display: inline-block;
  font-size: 2.4em;
  font-weight: 400;
  margin: 30px 0;
}
.email {
  font-size: 1.9em;
  font-weight: 500;
  margin-bottom: 15px;
}
.github {
  width: 100px;
  height: 90px;
}

/* 모바일 */
@media screen and (max-width: 767px){
  .email {font-size:1.4em;padding:0 15px;}
  .github {width:130px;height:auto;}
}
