@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR:100');
.skill {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  background: rgb(231, 230, 230);
  padding-top: 4vh;
  padding-bottom: 4vh;
}
.skill_title {
  font-family: 'Noto Sans KR', sans-serif;
  text-align: center;
  align-self: center;
  font-size: 4vw;
  margin: 3vh 0 2vh 0;
}

.skill_Content {
  margin-bottom: 20px;
}

.skill_image_container:hover .skill_label {
  visibility: visible;
  animation: fadeout1 0.5s;
  -moz-animation: fadeout1 0.5s; /* Firefox */
  -webkit-animation: fadeout1 0.5s; /* Safari and Chrome */
  -o-animation: fadeout1 0.5s; /* Opera */
}
@keyframes fadeout1 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}
@-moz-keyframes fadeout1 {
  /* Firefox */
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}
@-webkit-keyframes fadeout1 {
  /* Safari and Chrome */
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}
@-o-keyframes fadeout1 {
  /* Opera */
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}

/* 모바일 */
@media screen and (max-width: 767px) {
  .skill_Content {
    margin: 0;
    padding: 0 15px;
  }
  .skill_image_container {
    width: 27%;
    height: auto;
    margin: 5px;
    background-size: 100% 100%;
  }
  .skill_image_container img {
    width: 100%;
  }
  .skill_image_container:nth-child(3) {
    background-size: 90% 90%;
    background-position: center center;
  }
  .skill_label {
    width: 100%;
    height: 100px;
  }
}
